
ul.table {
  margin: 4rem auto 2rem auto;
  color: #34495e;
  font-size: 0.7rem;
  text-align: left;
}
ul.table li {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    border-radius: 2px;
    padding: 0.6rem 0;
    height: 1.6rem;
}
ul.table li span {
      -ms-flex-item-align: center;
          -ms-grid-row-align: center;
          align-self: center;
      padding-left: 0.6rem;
      line-height: 1.3;
}
ul.table li span.idx {
      width: 10px;
}
ul.table li span.name {
      width: 100px;
}
ul.table li span.age {
      width: 15px;
}
ul.table li span.city {
      width: 80px;
}
ul.table li:nth-child(odd) {
    background-color: #ecf0f1;
}



